.statistics {
  margin: 0;
  margin-top: 50px;
  margin-right: auto;
  margin-left: auto;
  background-color: #ffffff;
  width: 350px;
  text-align: center;
}
.title {
  margin: 0;
  padding: 0;
  padding: 20px;
}
.statList {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
}
.item {
  display: flex;
  flex-direction: column;
  width: calc(100% / 5);
  padding-top: 15px;
  padding-bottom: 15px;
  /* background-color: blue; */
}
.label {
  margin-bottom: 5px;
  color: #ffffff;
}
.percentage {
  color: #ffffff;
}
