.item {
  /* margin-right: auto;
  margin-left: auto; */
  display: flex;
  align-items: center;
  margin: 0;

  width: 250px;
  padding: 5px;
  /* border: 1px solid black; */
  box-shadow: 1px 1px 0px 0px;
  background-color: #fff;
}
.item:not(:last-child) {
  margin-bottom: 10px;
}
.avatar {
  margin-right: 15px;
}
.name {
}
.status {
  display: inline-block;
  /* width: 10px;
  height: 10px; */
  padding: 10px;
  margin-right: 5px;
  border-radius: 50%;
  /* background-color: rgb(255, 0, 0); */
}
.statusOn {
  composes: status;
  background-color: rgb(8, 90, 1);
}
.statusOff {
  composes: status;
  background-color: rgb(201, 0, 0);
}
