.transactionHistory {
  background-color: #fff;
  width: 600px;
  text-align: center;
  box-shadow: 5px 5px 4px 2px rgba(0, 0, 0, 0.1);
  border-collapse: collapse;
  border: 1px solid #fff;

  margin-top: 50px;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 50px;
}
.heading {
  margin: 0;
  width: 200px;

  background-color: rgb(8, 126, 126);
  /* border: 1px solid #fff; */
}
.headingName {
  width: 100%;
  padding-top: 15px;
  padding-bottom: 15px;
  width: calc(100% / 3);
  color: #fff;
  border: 1px solid #fff;
}
